import React, { useState } from 'react'
import MainScreen from '../../Component/MainScreen'
import { Button, Card, Form } from 'react-bootstrap'
import ErrorMsg from '../../Component/ErrorMsg'
import Loading from '../../Component/Loading'
import { useDispatch, useSelector } from 'react-redux'
import { createNoteAction } from '../../actions/noteActions'
import { useNavigate } from 'react-router-dom';
import ReactMarkDown from 'react-markdown';

const CreateNote = () => {
    const navigate = useNavigate();
    const [title, setTitle] = useState("");
    const [content, setContent] = useState("");
    const [category, setCategory] = useState("");

    const dispatch = useDispatch();

    const noteCreate = useSelector((state) => state.noteCreate);
    const {loading, error, note} = noteCreate;

    console.log(note);

    const resetHandler = () => {
        setTitle("");
        setCategory("");
        setContent("");
    };

    const submitHandler = (e) => {
        e.preventDefault();
        if(!title || !content || !category) return;
        dispatch(createNoteAction(title, content, category));
        

        resetHandler();
        navigate("/MyNotes");
    };
  return (
    <MainScreen title="Create a Note">
        <Card>
            <Card.Header>Create a new Note</Card.Header>
            <Card.Body>
                <Form onSubmit={submitHandler}>
                    {error && <ErrorMsg variant='danger' >{error}</ErrorMsg>}
                    <Form.Group className='mb-3' controlId='title'>
                        <Form.Label>Title</Form.Label>
                        <Form.Control type="title" value={title} placeholder="Enter the title" onChange={(e) => setTitle(e.target.value)} />
                    </Form.Group>

                    <Form.Group className='mb-3' controlId='content'>
                        <Form.Label>Content</Form.Label>
                        <Form.Control as="textarea" value={content} placeholder="Enter the content" rows={4} onChange={(e) => setContent(e.target.value)} />
                    </Form.Group>

                    {content && (
                        <Card>
                            <Card.Header>Note Preview</Card.Header>
                            <Card.Body>
                                <ReactMarkDown>{content}</ReactMarkDown>
                            </Card.Body>
                        </Card>
                    )}

                    <Form.Group className='mb-3' controlId='category'>
                        <Form.Label>Category</Form.Label>
                        <Form.Control type='content' value={category} placeholder="Enter the category" onChange={(e) => setCategory(e.target.value)} />
                    </Form.Group>
                    {loading && <Loading size={50} />}
                    <Button type="submit" variant="primary">Create Note</Button>
                    <Button className='mx-2' onClick={resetHandler} variant='danger' >Reset Feilds</Button>
                </Form>
            </Card.Body>

            <Card.Footer className='text-muted'>
                Creating on - {new Date().toLocaleDateString()}
            </Card.Footer>
        </Card>
    </MainScreen>
  )
}

export default CreateNote;